body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(41, 126, 144, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
    }


h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    color: blue;
}


